import genResource from 'angular-es-utils/rs-generator';
import Configs from '../../config.js';

export default {
  getTenant(tenantId, success, error) {
    const TenantResource = genResource(Configs.webPortal + '/:tenantId/system', false, {tenantId: '@tenantId'});
    TenantResource.get({tenantId: tenantId}, success, error);
  },
  login(params, success, error) {
     // const LoginResource = genResource(Configs.webPortal + '/credentials', false, {}, {
    const LoginResource = genResource('/web-portal/credentials', false, {}, {
      login: {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
        }
      }
    });
    LoginResource.login(params, success, error);
  }
};



