import angular from 'angular';

export default {
  restrict: 'E',
  scope: {
    login: '&action'
  },
  link: function(scope) {
    angular.element(document).bind('keypress', function(event) {
      if (event.which === 13) {
        scope.$apply(function() {
          scope.login();
        });
      }
    });
  }
};



