import angular from 'angular';
import $ from 'jquery';
import snabbtjs from '../../../assets/lib/snabbt.js';
import Service from './loginService';
export default class LoginCtrl {
  constructor($location, $ccTips) {
    angular.FSS('bg', 'output');
    var hostname = window.location.hostname;
    var tenantReg = null;
    if (hostname.indexOf('minicrm') !== -1) {
      tenantReg = /([^-.]+)\./;
    } else {
      tenantReg = /([^-.]+).(ccms|ff2e|s1|mini)/;
    };

    var execValue = tenantReg.exec(hostname);
    var tenantId = (execValue && execValue[1]) || 'yangyangyang3';
    // IE
    var browserName = navigator.userAgent.toLowerCase();
    if (/msie/i.test(browserName) && !/opera/.test(browserName)) {
      $ccTips.error('<a href=\'http://www.firefox.com.cn/download/\' title=\'\'></a><a href=\'http://www.google.cn/chrome/browser/desktop/index.html\' title=\'\'></a>');
    }
    const _this = this;
    _this.errorMarkFlag = false;
    _this.errorContent = '';
    _this.loginPlatName = '';
    _this.sendChoseValue = '';
    // 
    Service.getTenant(tenantId, function(data) {
      data = data || {};
      _this.defaultClassVal = true;
      _this.tenantId = data.tenantId;
      if (data.pageLoginEnabled) {
        _this.bottomPlatName = _this.loginPlatName = '';
        _this.canLoginSys = true;
      } else {
        _this.bottomPlatName = _this.loginPlatName = '';
        _this.canLoginSys = false;
        _this.userName = 'root';
        $('.userName input').val('root');
      }
      _this.tmlLoaded = true;
    }, function(res, status, headers, config) {
      $ccTips.error(res.data.message);
    });

    _this.defaultClassVal = true;
    _this.bottomPlatName = _this.loginPlatName = '';
    _this.canLoginSys = true;
    _this.tmlLoaded = false;

    _this.chosePlatVal = function(e) {
      if (!_this.canLoginSys) {
        return '';
      }
      var targetVal = e.target.innerHTML;
      var element = $(e.target);
      var element2 = element.siblings();
      var curStatus = true;
      if (_this.loginPlatName !== '') {
        curStatus = false;
      }

      if (!curStatus && targetVal === '') {
        _this.loginPlatName = '';
        snabbtjs(element.get(), {
          position: [0, 0, 0],
          fromRotation: [2, 2, 0]
        });
        snabbtjs(element2.get(), {
          position: [0, 0, 0],
          fromRotation: [2, 2, 0],
          opacity: 0.3
        });
        _this.defaultClassVal = true;
        _this.userName = '';
      } else if (curStatus && targetVal === '') {
        _this.loginPlatName = '';
        snabbtjs(element.get(), {
          position: [0, 60, 0],
          fromRotation: [2, 2, 0]
        });
        snabbtjs(element2.get(), {
          position: [0, -60, 0],
          fromRotation: [2, 2, 0],
          opacity: 0.3
        });
        _this.defaultClassVal = false;
        _this.userName = 'root';
      };
      $('.userName input,.userPassWord input').removeClass('error');
      _this.errorContent = '';
      _this.errorMarkFlag = false;
    };

    _this.loginMethod = function() {
      var dataObj = {
        'tenantId': _this.tenantId ? _this.tenantId : null,
        'name': _this.userName ? _this.userName : $('input[name=\'name\']').val(),
        'password': _this.userPassWord ? _this.userPassWord : $('input[name=\'password\']').val()
      };
      var errorMessage = [];
      /* start */
      if (dataObj.name === '' || dataObj.password === '') {
        if (dataObj.name === '') {
          $('.userName input').addClass('error');
          errorMessage.push('');
        }
        ;
        if (dataObj.password === '') {
          $('.userPassWord input').addClass('error');
          errorMessage.push('');
        }
        ;
        _this.errorContent = '' + errorMessage.join(',');
        _this.errorMarkFlag = true;
        return false;
      };

      window.tenantId = dataObj.tenantId;

      if (_this.defaultClassVal && _this.canLoginSys && dataObj.name.toUpperCase() === 'ROOT') {
        $('.userName input').addClass('error');
        _this.errorContent = 'root';
        _this.errorMarkFlag = true;
        return false;
      }

      /* end */
      var buttonBeforeAjaxVal = _this.loginPlatName;
      _this.loginPlatName = '...';
      Service.login($.param(dataObj), function(data, headers) {
        if (window.sessionStorage) {
          window.sessionStorage.clear();
        }
        console.log(headers());
        var setCookie = headers()['set-cookie'];
        if (setCookie) {
          console.log(setCookie);
          document.cookie = setCookie;
        }
        window.sessionStorage.setItem('username', dataObj.name || '');
        window.sessionStorage.setItem('userId', data.userId || '');
        window.sessionStorage.setItem('tenantId', dataObj.tenantId);
        window.sessionStorage.setItem('JSESSIONID', data.id || '');
        // localStorage token
        window.localStorage.setItem('ccmsRequestCredential', JSON.stringify(data));

        var contentUrl = '';
        if (_this.defaultClassVal && _this.canLoginSys) {
          contentUrl = '/portal/index.html#/dashboard';
        } else {
          contentUrl = '/portal/index.html#/systemManage/view/systemAccount';
        };
        location.replace(contentUrl);
      }, function(res, status, headers, config) {
        $('.userName input').addClass('error');
        $('.userPassWord input').addClass('error');
        _this.errorContent = res.data.message ? res.data.message : '';
        _this.errorMarkFlag = true;
        _this.loginPlatName = buttonBeforeAjaxVal;
      });
    };

    /* login input functions */
    $('div.loginInputWrapper').click(function() {
      $(this).find('.login_tip').css('display', 'none');
      $(this).find('input').focus();
    });
    $('div.loginInputWrapper').focusout(function() {
      if ($(this).find('input').val() <= 0) {
        $(this).find('.login_tip').css('display', 'block');
      }
    });
    $('div.loginInputWrapper').find('input').focusin(function() {
      $(this).parent('div.loginInputWrapper').find('.login_tip').css('display', 'none');
    });

    _this.inputFocus = function() {
      _this.errorMarkFlag = false;
      _this.errorContent = '';
      $('.userName input,.userPassWord input').removeClass('error');
    };
  }
};
LoginCtrl.$inject = ['$location', '$ccTips'];



